@font-face {
  font-family: prox-bold;
  src: url(../fonts/prox-bold.otf) format("opentype");
}

@font-face {
  font-family: prox-thin;
  src: url(../fonts/prox-thin.otf) format("opentype");
}

@font-face {
  font-family: prox-light;
  src: url(../fonts/prox-light.otf) format("opentype");
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0);
    -ms-transform: rotate(0);
    transform: rotate(0);
  }

  100% {
    -webkit-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotate(0);
    -ms-transform: rotate(0);
    transform: rotate(0);
  }

  100% {
    -webkit-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@-webkit-keyframes ring {
  0% {
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  33.333333% {
    -webkit-transform: rotate(5deg);
    -ms-transform: rotate(5deg);
    transform: rotate(5deg);
  }

  66.666666% {
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(-5deg);
    -ms-transform: rotate(-5deg);
    transform: rotate(-5deg);
  }
}

@keyframes ring {
  0% {
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  33.333333% {
    -webkit-transform: rotate(5deg);
    -ms-transform: rotate(5deg);
    transform: rotate(5deg);
  }

  66.666666% {
    -webkit-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(-5deg);
    -ms-transform: rotate(-5deg);
    transform: rotate(-5deg);
  }
}

.container {
  max-width: 1000px;
  padding: 0 15px;
  width: 100%;
  margin: 0 auto;
}

.container__big {
  max-width: 1280px;
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: prox-thin;
  background-color: #ffffff;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

a:focus,
.button:focus {
  outline: none;
}

.button {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  background-color: #ed1c34;
  color: #ffffff;
  border: none;
  -webkit-box-shadow: inset 0 0 0 2px #ed1c34;
  box-shadow: inset 0 0 0 2px #ed1c34;
  padding: 15px;
  cursor: pointer;
  font-size: 14px;
  font-family: prox-bold;
  text-transform: uppercase;
  line-height: 1em;
  text-decoration: none;
  display: inline-block;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.button:hover {
  color: #ed1c34;
  background-color: #ffffff;
}

.button__with-icon {
  padding: 15px 30px 15px 65px;
  position: relative;
}

.button__with-icon:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 32px;
  width: 22px;
  height: 22px;
  margin-left: -11px;
  margin-top: -11px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  background-position: center center;
  -webkit-background-size: 22px 22px;
  background-size: 22px 22px;
}

.button__with-icon-connect:before {
  background-image: url(../images/icon-connect.png);
}

.button__with-icon:hover {
  background-color: #ed1c34;
  color: #ffffff;
}

.button__with-icon:hover:before {
  -webkit-transform: scale(1.3);
  -ms-transform: scale(1.3);
  transform: scale(1.3);
}

.header {
  font-size: 0;
  background-color: #1a191a;
}

.header-top {
  position: relative;
  padding-bottom: 5px;
}

.header-top:before,
.header-top:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: #636363;
}

.header-top:after {
  bottom: -1px;
  display: none;
  background-color: #d9d5d1;
}

.header__over-banner {
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(151, 128, 108, 0.6);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  -webkit-box-shadow: inset 0px -15px 15px -10px #73625b;
  box-shadow: inset 0px -15px 15px -10px #73625b;
}

.header__over-banner .header-top:before {
  background-color: #73625b;
}

.header__over-banner .header-top:after {
  display: block;
}

.header-left {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  max-width: 183px;
}

.header-left a {
  display: block;
  padding: 10px 0;
}

.header-left img {
  max-width: 100%;
  height: auto;
}

.header-middle {
  padding-left: 35px;
  width: -o-calc(100% - 318px);
  width: -webkit-calc(100% - 318px);
  width: calc(100% - 318px);
  display: inline-block;
  vertical-align: middle;
}

.header-right {
  display: inline-block;
  vertical-align: middle;
  width: 135px;
}

.header-connect {
  display: inline-block;
  vertical-align: middle;
  padding: 0 20px;
  width: 40%;
}

.header-phone {
  display: inline-block;
  vertical-align: middle;
  padding: 0 20px;
  width: 60%;
}

.header-phone a {
  font-size: 30px;
  text-decoration: none;
  color: #ffffff;
  font-family: prox-light;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.75);
  position: relative;
  padding-left: 40px;
  display: inline-block;
}

.header-phone a:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -16.5px;
  width: 33px;
  height: 33px;
  background-position: center cneter;
  background-repeat: no-repeat;
  -webkit-background-size: 33px 33px;
  background-size: 33px 33px;
  background-image: url(../images/icon-phone.png);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.header-phone a:hover:before {
  -webkit-animation: ring linear .15s infinite;
  animation: ring linear .15s infinite;
}

.header-login {
  padding-left: 20px;
  display: inline-block;
  vertical-align: middle;
}

.header-login a {
  display: block;
  font-size: 18px;
  font-family: prox-light;
  color: #ffffff;
  text-decoration: none;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  padding-left: 48px;
  position: relative;
  line-height: 1em;
}

.header-login a:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -19px;
  width: 38px;
  height: 38px;
  background-position: center cneter;
  background-repeat: no-repeat;
  -webkit-background-size: 38px 38px;
  background-size: 38px 38px;
  background-image: url(../images/icon-login.png);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.header-login a:hover:before {
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}

.footer {
  background-color: #1a191a;
  padding: 60px 0;
  font-size: 0;
}

.footer-block {
  display: inline-block;
  vertical-align: top;
}

.footer-left {
  width: 40%;
  padding-left: 15px;
}

.footer-phone a {
  font-size: 30px;
  text-decoration: none;
  color: #ffffff;
  font-family: prox-light;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.75);
  position: relative;
  padding-left: 40px;
  display: inline-block;
}

.footer-phone a:before {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -16.5px;
  width: 33px;
  height: 33px;
  background-position: center cneter;
  background-repeat: no-repeat;
  -webkit-background-size: 33px 33px;
  background-size: 33px 33px;
  background-image: url(../images/icon-phone.png);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.footer-phone a:hover:before {
  -webkit-animation: ring linear .15s infinite;
  animation: ring linear .15s infinite;
}

.footer-title {
  text-transform: uppercase;
  font-size: 14px;
  font-family: prox-bold;
  color: #f1f1f1;
  margin-top: 0;
  margin-bottom: 8px;
}

.footer-rights {
  color: #696969;
  font-size: 12px;
  font-family: prox-light;
  margin-top: 15px;
}

.footer-navigation {
  padding: 0 15px;
  width: 20%;
}

.footer-socials {
  padding: 0 15px;
  width: 20%;
}

.footer-madeby {
  width: 20%;
  text-align: right;
}

.footer-madeby span {
  font-size: 12px;
  color: #ffffff;
  font-family: prox-light;
  position: relative;
  left: -43px;
  margin-bottom: 10px;
  display: inline-block;
}




